
.artical{
  margin-top:5rem;
  font-size:1.6rem;
  border:.1rem solid #fff;
  font-size:1.7rem;
  padding:0 .5rem;
}
.artical .header{
    position:fixed;
    right:0;
    left:0;
    top:0;
    height:5rem;
    line-height:5rem;
    font-size:2.5rem;
    background:#fff;
    text-align:center;
    border-bottom:.1rem solid #ddd;
}
.artical  .arrow{
    width:5rem;
    height:5rem;
    float:left;
    background:url("../assets/img/arrow.png") no-repeat center center;
}
.artical  p{
    text-indent:3.4rem;
}
.artical  img{
    width:100%;
}



