
.login .header{
    position:fixed;
    right:0;
    left:0;
    top:0;
    height:5rem;
    line-height:5rem;
    font-size:2.5rem;
    background:#fff;
    text-align:center;
    border-bottom:.1rem solid #ddd;
}
.login  .arrow{
    width:5rem;
    height:5rem;
    float:left;
    background:url("../assets/img/arrow.png") no-repeat center center;
}
.login  .content{
    margin-top:10rem;
}
.login  input{
    width:80%;
    height:5rem;
    border-radius:2%;
    padding-left:4rem;
    font-size:2rem;
    margin-top:1rem;
    outline:none;
}
.login  .phone{
    background:url("../assets/img/user2.png") no-repeat left center ;
}
.login  .upwd{
     background:url("../assets/img/upwd.png") no-repeat left center;
}
.login  button{
    width:92%;
    height:5rem;
    background:#00B369;
    outline:none;
    border:none;
    font-size:2rem;
    color:#fff;
    margin-top:1rem;
}
.login  .help_box,.help_phone{
    padding:.5rem 3rem 0 3rem;
    font-size:1.5rem;
    text-align:left;
    color:#FF714E;
    height:1.5rem;
}
.login  .loginSucc{
    position:fixed;
    top:0;
    bottom:0;
    right:0;
    left:0;
    background:rgba(0,0,0,.4);
}
.login .sContent{
    width:60%;
    height:16rem;
    background:#F1F1F1;
    border-radius:1rem;
    position:absolute;
    top:35%;
    left:20%;
    line-height:15rem;
    font-size:2rem;
}
.login .toReg{
    line-height:5rem;
    font-size:1.3rem;
    padding-left:2rem;
    text-align:left;
}



