
.cart .header{
    position:fixed;
    right:0;
    left:0;
    top:0;
    height:5rem;
    line-height:5rem;
    font-size:1.8rem;
    background:#fff;
    text-align:center;
}
.cart .header i{
    position:fixed;
    top:0;
    left:0;
}
.cart .arrow{
    width:5rem;
    height:5rem;
    float:left;
    background:url("../assets/img/arrow.png") no-repeat center center;
}
.cart .content{
    font-size:1.4rem;
    color:#9A9A9A;
    background:#fff;
    position:fixed;
    right:0;
    left:0;
    top:5rem;
    overflow:visible;
}
.cart .itemList{
    height:auto;
}
.cart .pEname,.pName{
    color:#000 !important;
}
.cart .dishWare img{
    vertical-align:middle;
}
.cart .handle{
    float:right;
}
.cart .handle span{
    display:inline-block;
    width:3rem;
    height:3rem;
    line-height:3rem;
    background:#F0F0F0;
    text-align:center;
}
.cart .handle img{
    width:45%;
    vertical-align:middle;
}
.cart .sPrice{
    float:left;
}
.cart .detail>p{
    font-size:1.6rem;
}
.cart .choseNum{
    line-height:3rem;
    margin-top:1rem;
}
.cart .footer{
    position:fixed;
    right:0;
    left:0;
    bottom:0;
    height:5rem;
    background:#fff;
    line-height:5rem;
    text-align:right;
    font-size:1.6rem;
}
.cart .toBuy{
    position:absolute;
    right:0;
    top:0;
    width:60%;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
.cart .pay{
    display:inline-block;
    width:55%;
    height:100%;
    background:#000;
    font-size:2rem;
    color:#fff;
    text-align:center;
}
.cart .toPrice{
    display:inline-block;
    width:35%;
    height:100%;
    padding-right:1rem;
    font-size:1.8rem;
}
.cart .item{
    width:100%;
    padding:1rem 2rem 1rem 2rem;
    text-align:left;
    background:#fff;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
.cart .item:after{
    content:"";
    display:block;
    clear:both;
}
.cart .item_box{
    -webkit-box-shadow:0rem .1rem .5rem rgba(0,0,0,.5);
            box-shadow:0rem .1rem .5rem rgba(0,0,0,.5);
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
.cart .itemPic{
    width:40%;
    float:left;
    overflow:hidden;
}
.cart .itemPic img{
    width:100%;
}
.cart div.detail{
    padding-left:1.8rem;
    float:left;
    width:50%;
}
.cart p.dishWare{
    float:left;
    padding-right:2rem;
    width:100%;
}
.cart .noItem{
    font-size:1.8rem;
}
.cart .toMain{
    text-decoration:underline;
    color:#DE5246;
}



