
.banner{
    padding-top:5rem;
}
.banner  .swiper-box {
    width: 100%;
    height: 20rem;
    margin: 0 auto;
}
.banner  .swiper-item {
    height: 100%;
    text-align: center;
    font-size: 18px ;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.banner  .swiper-item img{
      width:100%;
}



