
.logo{
  width:20%;
}
#header{
  position:fixed;
  top:0;
  right:0;
  left:0;
  -webkit-box-shadow: 0  0.013333rem  0.026667rem rgba(0,0,0,.1);
          box-shadow: 0  0.013333rem  0.026667rem rgba(0,0,0,.1);
  background:#fff;
  z-index:10;
}
#main{
  font-size:1.6rem;
  border:.1rem solid #fff;
}
#main  .topTitle{
  font-size:1.8rem;
  line-height:5rem;
}
#main .classify{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-wrap:nowrap;
        flex-wrap:nowrap;
    width:100%;
}
#main .top{
   display:-webkit-box;
   display:-ms-flexbox;
   display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;
   -webkit-box-pack:justify;
       -ms-flex-pack:justify;
           justify-content:space-between;
   padding:0 1rem;
}
#main .topItem{
  border-radius:1.5rem;
  width:49%;
}
#main .topItem img{
  width:100%;
}




