
.register .header{
    position:fixed;
    right:0;
    left:0;
    top:0;
    height:5rem;
    line-height:5rem;
    font-size:2.5rem;
    background:#fff;
    text-align:center;
    border-bottom:.1rem solid #ddd;
}
.register .arrow{
    width:5rem;
    height:5rem;
    float:left;
    background:url("../assets/img/arrow.png") no-repeat center center;
}
.register .content{
    margin-top:10rem;
}
.register form{
    margin:5rem auto;
    width:70%;
}
.register label{
    float:left;
    font-size:1.6rem;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
.register span{
    display:block;
    text-align:left;
    padding:0 3rem 0 0;
    font-size:1.5rem;
    color:#FF714E;
    height:1.5rem;
}
.register  input{
    width:90%;
    height:4rem;
    border-radius:2%;
    font-size:2rem;
    margin-top:.2rem;
    outline:none;
    padding-left:3.5rem;
}
.register  input{
    outline:none;
}
.register input[type=button]{
    width:105%;
    height:5rem;
    background:#00B369;
    outline:none;
    border:none;
    font-size:2rem;
    color:#fff;
    margin-top:1rem;
    padding:0 !important;
}
.register .uname{
    background:url("../assets/img/user2.png") no-repeat left center;
}
.register .phone{
    background:url("../assets/img/phone.png") no-repeat left center;
}
.register .upwd{
    background:url("../assets/img/pwd.png") no-repeat left center;
}
.register .help_box{
    height:2rem;
}
.register .canUse{
    color:#009B69 !important;
}
.register .regSucc{
    position:fixed;
    top:0;
    bottom:0;
    right:0;
    left:0;
    background:rgba(0,0,0,.4);
}
.register .rContent{
    width:60%;
    height:16rem;
    background:#F1F1F1;
    border-radius:1rem;
    position:absolute;
    top:35%;
    left:20%;
    line-height:15rem;
    font-size:2rem;
}



