
.goodlist{
    margin-top:9.5rem;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-pack:justify;
        -ms-flex-pack:justify;
            justify-content:space-between;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    padding:0 1rem;
}
.goodlist .items{
    width:46%;
    padding:1rem 0;
}
.goodlist .items img{
    width:100%;
}
.goodlist.icon_cart{
    width:1.5rem;
    height:1.5rem;
    display:inline-block;
    position:relative;
    right:-1rem;
    bottom:-.5rem;
    background:url("../assets/img/cart_b.png") no-repeat center center;
    z-index:1;
}



